<template>
  <!-- 购买任务 -->
  <div class="tasks">
    <el-card class="box-card tasks-card" :body-style="{ padding: '0 0 20px'}">
      <div slot="header" class="clearfix">
        <span class="form-title">{{ $t('tasks.purchaseTasks') }}</span>
      </div>
      <el-row class="tasks-info">
        <el-col :lg="24">
          <h5 v-html="$t('tasks.tasksRemind')"></h5>
          <h5>{{ $t('tasks.adressTitle') }}</h5>
          <h5 style="word-break: break-all;">{{ purchaseTask.tasksAdress }}</h5>
          <h5>{{ $t('tasks.tasksPurchase') }}{{ purchaseTask.tasksPurchase === undefined ? 0 : purchaseTask.tasksPurchase}} ETH</h5>
          <h5>{{ $t('tasks.hadPurchase') }}{{ purchaseTask.hadPurchase === undefined ? 0 : purchaseTask.hadPurchase}} ETH</h5>
          <h5>{{ $t('tasks.needSpend') }}{{ purchaseTask.needSpend === undefined ? 0 : purchaseTask.needSpend}} ETH</h5>
          <h5>{{ $t('tasks.lockNumber') }} {{ purchaseTask.lockStraight === undefined ? 0 : purchaseTask.lockStraight }} ETH</h5>
        </el-col>
      </el-row>
      <div class="card-middle">
        <el-row type="flex" justify="space-between">
          <el-col :span="8">
            <el-image class="eth-img" :src="ethUrl" fit="fill"></el-image>
          </el-col>
          <el-col :span="8">
            <el-input
              :placeholder="$t('purchase.placeholder')"
              v-model="inputPurchase"
              type="number"
              class="input-style"
            />
          </el-col>
          <el-col :span="8">
            <el-image class="yellowkey-img" :src="yellowkeyUrl" fit="fill"></el-image>
          </el-col>
        </el-row>
        <div class="box-btn">
          <el-button round @click="purchaseBuy()" class="tasks-btn">{{ $t('commom.btnPurchase') }}</el-button>
        </div>
      </div>
    </el-card>

    <el-card class="support-card form-card" :body-style="{ padding: '0px'}">
      <div slot="header" class="clearfix">
        <span class="form-title">{{$t('tasks.support')}}</span>
        <el-button style="padding: 5px 30px;float: right" type="text" @click="filtSupportedStatus([1])">仅显示可扶持</el-button>
        <!-- <el-button style="padding: 3px 0;color: #f9d87c" type="text" @click="filtSupportedStatus([3])">仅显示已扶持</el-button>
        <el-button style="padding: 3px 0" type="text" @click="filtSupportedStatus([2])">仅显示已过期</el-button> -->
        <el-button style="padding: 5px 0;float: right" type="text" @click="filtSupportedStatus([1, 2, 3])">显示全部</el-button>
      </div>
      <div>
        <el-table :data="recommendRecord" style="width: 100%" height="350" size="mini">
          <el-table-column
            prop="oldTime"
            :label="$t('commom.date')"
             width="90"
            sortable
            align="center"
          ></el-table-column>
          <el-table-column
            prop="eth"
            label="ETH"
             width="80"
            sortable
            align="center"
          ></el-table-column>
          <el-table-column
            prop="leftDays"
            :label="$t('commom.leftday')"
            width="80"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="address"
            :label="$t('commom.address')"
            align="center"
            show-overflow-tooltip
          ></el-table-column>
          <el-table-column
            label="Supported"
            align="center"
            width="120"
            show-overflow-tooltip
          >
<!--         :filters="[{ text: '可扶持', value: 1 }, { text: '已过期', value: 2 }, { text: '已扶持', value: 3 }]"
            :filter-method="filterSupport"
            filter-placement="bottom-end"-->
            <template slot-scope="scope"><!--todo 中英文切换 8.07 待做-->
              {{ scope.row.supportedStatus === 1 ? $t('tasks.supportStatus1') : (scope.row.supportedStatus === 2 ? $t('tasks.supportStatus2') : $t('tasks.supportStatus3')) }}
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            align="center"
            :label="$t('commom.action')"
            width="70"
          >
            <template slot-scope="scope"><!--todo 如果已扶持，则不可在点击 8.07-->
              <el-button
                @click="supportReward(scope.row.index, scope.row.address, scope.row.originalEth)"
                type="text"
                size="mini"
                class="action-btn"
                :disabled="purchaseTask.needSpend <= 0 ? (!scope.row.actionStatus) : true"
              >{{$t('tasks.supportAction') }}</el-button> <!--todo 8.07-->
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
import { checkUrlParam } from '../Mixin/checkUrlParam'
import {urlFilter} from '../Mixin/urlFilter'
import ethUtils from '../utils/ethUtils'
import store from '../store/index'
import {metaMaskInfo} from '../Mixin/metaMaskInfo'

export default {
  name: 'Tasks',
  mixins: [checkUrlParam, urlFilter, metaMaskInfo],
  data () {
    return {
      filtCondition: [1], // 默认只显示可扶持
      radio: 1,
      ethUrl: require('../assets/img/ETH.png'),
      yellowkeyUrl: require('../assets/img/yellowkey.png'),
      inputPurchase: 0
    }
  },
  computed: {
    purchaseTask () {
      const purchaseTasksInfo = this.$store.state.contract.purchaseTasksInfo
      return purchaseTasksInfo
    },
    recommendRecord () {
      // todo 审核taskInfo 的 needSpend是否<= 0, 否则不可以操作
      const record = this.$store.state.contract.recommendRecord
      if (this.filtCondition.length === 1) {
        if (this.filtCondition[0] === 1) {
          return record.filter(el => el.supportedStatus === 1)
        } else if (this.filtCondition[0] === 2) {
          return record.filter(el => el.supportedStatus === 2)
        } else {
          return record.filter(el => el.supportedStatus === 3)
        }
      } else {
        return record
      }
    },
    commitMark: function () {
      return this.$store.state.metamask.commitMark
    }
  },
  watch: {
    commitMark: function (newVal) {
      const status = this.$store.state.metamask.status
      if (status === 1) {
        this.$store.dispatch('contract/getPurchaseTasksInfo')
        this.$store.dispatch('contract/getRecommendRecord')
      }
    }
  },
  methods: {
    filtSupportedStatus (val) {
      this.filtCondition = val
    },
    async purchaseBuy () { // purchase页面 purchase操作，buy接口 todo 没有使用hex进制, 需要测试
      const data = {} // 交易数据
      data.referralAddress = this.checkUrlParam()
      if (!data.referralAddress) {
        this.$notify({
          title: this.metaInfo.warning,
          message: this.metaInfo.checkAddress,
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      // 非正过滤
      data.value = this.inputPurchase
      this.inputPurchase = 0 // 获取输入框中的值后清空输入框
      if (data.value <= 0) {
        this.$notify({
          title: this.metaInfo.warning,
          message: this.metaInfo.checkVal,
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      // 精度过滤
      const decimalLimit = this.$ethConf.decimalLimit
      const len = data.value.toString().split('.').length
      if (len === 2) {
        if (data.value.toString().split('.')[1].length > decimalLimit) {
          this.$notify({
            title: this.metaInfo.warning,
            message: this.metaInfo.checkFloat,
            position: 'top-left',
            type: 'warning'
          })
          return
        }
      }
      const status = this.$store.state.metamask.status
      if (status !== 1) {
        this.$notify({
          title: this.metaInfo.warning,
          message: this.metaInfo.loginWallet,
          type: 'warning'
        })
      } else {
        const myProvider = this.$myProvider // todo 使用myProvider，测试通过的话，可以删除 vuex networkVersion
        const networkVersion = myProvider.networkVersion
        if (networkVersion !== this.$ethConf.networkVersion) { // 过滤，非指定网络交易
          this.$notify({
            title: this.metaInfo.warning,
            message: this.metaInfo.networkVersion,
            type: 'warning'
          })
        } else {
          const contractRes = this.$myContracts && this.$myContracts.contractRes // Resonance合约地址 todo 标记 引用合约
          data.gasLimit = '0x2dc6c0' // todo 3 gaslimit
          if (contractRes) {
            data.from = myProvider.selectedAddress
            contractRes.methods.buy(data.referralAddress).send({
              from: data.from,
              gas: data.gasLimit,
              value: ethUtils.ethToWei(data.value)
            }, function (error, result) {
              if (error) {
                console.error(error)
              } else {
                // 更新purchaseTasksInfos数据 7/24
                store.dispatch('contract/getPurchaseTasksInfo')
              }
            })
          }
        }
      }
    },
    // 市场扶持奖励 接口：supportSubordinateAddress 合约地址：Resonance合约地址
    async supportReward (index, subordinateAddress, value) { // todo time: 过滤，距离当前时间超过三天的不可已扶持；index:索引， subordinateAddress下级地址， 即getRecommendRecord接口返回的直推地址; value的10%是扶持的数量
      const data = {} // 交易数据
      const referralAddress = this.checkUrlParam()
      if (!referralAddress) {
        this.$notify({
          title: this.metaInfo.warning,
          message: this.metaInfo.checkAddress,
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      const status = this.$store.state.metamask.status
      if (status !== 1) {
        this.$notify({
          title: this.metaInfo.warning,
          message: this.metaInfo.loginWallet,
          type: 'warning'
        })
      } else {
        const myProvider = this.$myProvider
        const networkVersion = myProvider.networkVersion
        if (networkVersion !== this.$ethConf.networkVersion) { // 过滤，非指定网络交易
          this.$notify({
            title: this.metaInfo.warning,
            message: this.metaInfo.networkVersion,
            type: 'warning'
          })
        } else {
          const contractRes = this.$myContracts && this.$myContracts.contractRes // Resonance合约地址 todo 标记 引用合约
          data.gasLimit = '0xc3500' // todo 4
          if (contractRes) {
            data.from = myProvider.selectedAddress
            let supportValue = ethUtils.ethToWei(value) // todo value的值需要使用原始值
            data.value = ethUtils.supportReward(supportValue) // 直推投资数量的10%
            contractRes.methods.supportSubordinateAddress(index, subordinateAddress).send({
              from: data.from,
              gas: data.gasLimit,
              value: data.value
            }, function (error, result) {
              if (error) {
                console.error(error)
              } else {
                store.dispatch('contract/getRecommendRecord')
              }
            })
            /// this.$store.commit('contract/UPDATE_ACTIONSTATUS', index) // todo 8.07 待定
          }
        }
      }
    }
  },
  created () {
    const status = this.$store.state.metamask.status // 获取状态在后置导航守卫，可能会出现状态还没获取就进入了路由;解决：全局监听
    if (status === 1) { // 没有对应的订阅配合，所以每次进入路由都需要获取最新数据
      this.$store.dispatch('contract/getPurchaseTasksInfo')
      this.$store.dispatch('contract/getRecommendRecord')
    }
    // todo 提示用户，只有need purchase <=0时，才可以进行扶持 8.07;已解决，在投资任务部分解决
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/variables.scss";
.tasks {
  height: 100%;
  font-size: 18px;
  .tasks-card {
    .tasks-info {
      padding: 10px 30px;
      color: $title;
      background: linear-gradient(to bottom, #efdbcb, #ffd647);
      h5 {
        text-align: left;
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        text-decoration: none;
      }
    }
    .card-middle {
      text-align: center;
      padding: 20px 10px 0;
      i {
        font-size: 70px;
      }
      .input-style {
        padding: 15px 0;
      }
    }
  }
  .box-btn {
    margin: 20px 0;
  }
  .action-btn {
    color: #409eff;
    width: 50px;
    font-size: 12px;
    font-weight: 500;
  }
}
</style>
<style scoped>
@import "../assets/table.css";
.el-card {
  margin-top: 1.5rem;
  background-color: #252323;
  border: 0px solid #252323;
  color: #dac28f;
}
.box-card >>> .el-card__header {
  padding: 8px 20px;
  background: linear-gradient(to bottom, #2c2c2c, #242121);
  font-size: 16px;
  border: 0px solid #f9d76c;
}
.form-card >>> .el-card__header {
  padding: 8px 20px;
  background: linear-gradient(to bottom, #2c2c2c, #242121);
  color: #f9d76c;
  font-size: 16px;
  border: 0px solid #f9d76c;
}
.el-input >>> .el-input__inner {
  text-align: center;
  background-color: #0f0d0d;
  border: 0px solid #0f0d0d;
  border-radius: 18px;
  color: #DAC28F;
  font-size: 18px;
}
.tasks-btn {
  width: 120px;
  font-size: 18px;
  background-color: #f8d87c;
  border-radius: 18px;
  border-width: inherit;
  border-color: #666666;
  font-family: ArialMT;
  font-weight: bold;
  color: #000000;
}
</style>



